body {
  position: relative;
}

body.open-modal {
  overflow-y: hidden;
}

body.open-modal .overlay, body.open-modal .modal-window {
  display: block;
}

.overlay {
  background-color: #000000b7;
  display: none;
  position: fixed;
  inset: 0;
}

.modal-window {
  max-width: 640px;
  width: 90%;
  background-color: #e6e0e0;
  border-radius: 4px;
  padding: 24px;
  transition: transform .2s ease-in-out;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.close-btn {
  color: #4fe6bb;
  background-color: #d16243;
  border: none;
  font-size: 1.2rem;
  box-shadow: 0 2px 5px #d32525b3;
}

